.products {
  padding: 66px 0 140px;
  background-color: $color-default-black;

  @include vp-1023 {
    padding: 55px 0 75px;
  }

  @include vp-767 {
    padding: 42px 0 86px;
  }
}

.products img {
  position: absolute;
  width: 1494px;
  height: 818px;
  max-width: none;
  bottom: -51px;
  left: -534px;
  object-fit: cover;
  z-index: -1;


  @include vp-1023 {
    left: auto;
    bottom: -253px;
    right: -195px;
  }

  @include vp-767 {
    bottom: -30px;
    right: auto;
    left: -365px;
  }
}

.products h2 {
  margin: 0;
  margin-bottom: 51px;
  font-family: $ff-fancy;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;
  color: $color-default-white;

  @include vp-1023 {
    margin-bottom: 38px;
  }

  @include vp-767 {
    margin-bottom: 28px;
    font-size: 28px;
    line-height: 41px;
  }
}

.products h2 span:nth-child(2) {
  display: none;

  @include vp-767 {
    display: block;
  }
}

.products h2 span:nth-child(1) {
  @include vp-767 {
    display: none;
  }
}

.products ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, minmax(250px, 350px));
  grid-auto-rows: 650px;
  gap: 85px;

  @include vp-1023 {
    grid-template-columns: 1fr;
    grid-auto-rows: 250px;
    gap: 30px;
  }

  @include vp-767 {
    grid-template-columns: auto;
  }
}

.products li {
  position: relative;
  min-height: 650px;
  z-index: 1;
  background-color: $color-default-white-transparent;

  @include vp-1023 {
    min-height: 250px;
  }


  @include hover-focus {
    opacity: 0.9;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    width: 60px;
    height: 60px;
    background-color: $color-deep-sky-blue;
    background-image: url("../img/svg/icon-next.svg");
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 3px 0 0;
    cursor: pointer;

    @include vp-767 {
      width: 40px;
      height: 40px;
    }
  }
}

.products li:nth-child(3n+1) img {
  left: -187px;

  @include vp-1023 {
    bottom: -18px;
    left: -244px;
  }

  @include vp-767 {
    bottom: -268px;
    left: -375px;
  }
}

.products li:nth-child(3n+3) img {
  left: auto;
  right: -249px;
  bottom: -61px;

  @include vp-1023 {
    right: auto;
    left: -148px;
    bottom: -208px;
  }

  @include vp-767 {
    left: auto;
    right: -339px;
    bottom: -173px;
  }
}

.products a {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-image: $gray-transparent-cover;
}

.products h3 {
  display: block;
  max-width: 180px;
  margin: 0 40px;
  padding-top: 50px;
  font-family: $ff-fancy;
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
  color: $color-default-white;

  @include vp-1023 {
    max-width: 150px;
    margin-left: 45px;
    padding-top: 40px;
  }

  @include vp-767 {
    max-width: 135px;
    font-size: 18px;
    margin: 0 30px;
    padding-top: 27px;
  }

  @include hover-focus {
    color: $color-sky-blue;
  }
}
